  async mounted() {
    this.$root.setMeta({ title: this.title });
    // const response = await this.getSingleTask(this.taskId);
    // if (response?.success) this.taskInfo = response.data;
    // console.warn('taskInfo', this.taskInfo, this.taskId);
    this.tabSectionId = this.hashId;
    this.updateLightBox();
    this.$nextTick(() => {
      setTimeout(() => {
        this.documentReady = true;
      }, 1000);
    });
  },

  computed: {
    ...mapState(useUser, ['selectedOrgBrands', 'selectedOrgTasks']),

    hashId() {
      return this.$route.hash.slice(1);
    },

    brandId() {
      return this.$route.params?.brandId;
    },

    taskId() {
      return this.$route.params?.taskId;
    },

    brandInfo() {
      return this.selectedOrgBrands[this.brandId] || {};
    },

    overviewData() {
      return this.taskInfo?.dynamic_questions?.filter?.(
        (e) => e.show_in_overview
      );
    },

    overviewIds() {
      return this.overviewData.map((e) => this.convertToKey(e.title));
    },

    allSections() {
      return this.overviewData.map((e, i) => ({
        label: e.title,
        key: this.overviewIds[i],
      }));
    },
  },

  watch: {
    editing: {
      async handler(val) {
        setTimeout(this.updateLightBox, 500);
      },
    },
    tabSectionId: {
      async handler(val) {
        if (val) {
          // this.$router.push({ hash: '#' + val });
          this.scrollToTarget(val);
          this.tabSectionId = '';
          // this.tabSectionIdScrolled = '';
        }
      },
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['getSingleTask']),

    updateLightBox() {
      this.lightboxImages = [];
      const lightboxElems = document.querySelectorAll('.with-lightbox');
      lightboxElems.forEach((e, i) => {
        let img = e.querySelector('img');
        this.lightboxImages.push({
          src: img.src,
          title: img.title,
          alt: img.alt,
        });
        e.onclick = () => {
          this.lightboxIndex = i;
          this.showLightBox = true;
        };
      });
    },

    scrollToTarget(targetId) {
      getEl(targetId)?.scrollIntoView?.({
        block: 'start',
        inline: 'nearest',
        behavior: 'smooth',
      });
    },

    clickScrollEvent(targetId, copy = true) {
      this.tabSectionId = targetId;
      // Copy URL to Clipboard
      if (copy) {
        copyToClipboard(
          window.location.origin + this.$route.fullPath + '#' + targetId
        );
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Anchor has been copied to clipboard.',
          timeout: 500,
        });
      }
    },

    scrollHandlerOrig(scroll) {
      this.tabSectionIdScrolled = '';
      let selectedKey = '';
      this.allSections
        .map((e) => e.key)
        .reverse()
        .forEach((key) => {
          const el = getEl(key),
            offsetTop = el?.offsetTop + 25,
            top = scroll?.position?.top;
          if (top > offsetTop && !selectedKey) {
            selectedKey = key;
          }
        });
      this.tabSectionIdScrolled = selectedKey;
    },
  },
});
</script>
